import React from "react";
import "./style.css";
import WeatherIcon from "./WeatherIcon";
import Toggle from 'react-toggle';

export default class WeatherInfo extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            on: false
        }
        this.handleSwitchChange = this.handleSwitchChange.bind(this);
    }
    componentWillReceiveProps() {
        this.setState({
            on: this.props.searchText === '' ? false : this.state.on
        })
    }
    handleSwitchChange(){
        this.setState({
            on: !this.state.on
        })
    }
    render() {
        return (
            <div>
                {(this.props.status === "loaded") ? (
                    <div>
                        <p>{this.props.description}</p>
                        {this.state.on ? (
                            <p>{this.props.tempC}&#176;C</p>
                        ) : (
                                <p>{this.props.tempF}&#176;F</p>
                            )}
                        <label>
                            <Toggle
                                defaultChecked={this.state.on}
                                onChange={this.handleSwitchChange} 
                                icons={{
                                    checked: <span>&#176;C</span>,
                                    unchecked: <span>&#176;F</span>,
                                  }}
                                />
                        </label>

                        <p>{this.props.address}</p>
                        <WeatherIcon weatherID={this.props.weatherMain} />
                    </div>
                ) : (
                        <p>{this.props.status}</p>
                    )}
            </div>
        );
    }
}
