let getWeather = async function(lat, long) {
    var weatherAPI = `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${long}&units=metric&appid=924d4013269e4852627c3106302c806b`;
    try {
        let response = await fetch(weatherAPI);
        let data = await response.json();
        return data;
    } catch (e) {
        return e;
    }
}

let getAddress = async function(lat, long) {
    try {
        let response = await fetch(`https://maps.googleapis.com/maps/api/geocode/json?latlng=${lat},${long}&key=AIzaSyB2cgSHq1pOt88qxuDRRaD7spdNZsemuGQ`);
        let data = await response.json();
        return data;
    } catch (e) {
        return e;
    }
}

export { getWeather, getAddress };


